import React, { Component } from "react";
import Slider from "react-slick";


export default class SimpleSlider extends Component {
  render() {
    var settings = {
      className: "banner",
      dots: true,
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: true
    };
    return (
        <Slider {...settings}>
          <div>
              <img src='https://ae01.alicdn.com/kf/H69740ae780124d9db63874b139166fc4J.jpg_Q90.jpg_.webp'/>
          </div>
          <div>
              <img src='https://ae01.alicdn.com/kf/H134d681887c2406981c9485014570ca2Z.png_.webp'/>
          </div>
          <div>
              <img src='https://ae01.alicdn.com/kf/H3c9abd9248bc4187a58717b6a302c8a1l.png_.webp'/>
          </div>
        </Slider>
    );
  }
}